<template>
  <div>
    <div class="div_emploter">
      <div class="employer" >雇主信息</div>
      <div class="employer_info">
        <span>姓名：{{detailsData.user ? detailsData.user.name : ''}}</span>
        <span>手机号码：{{detailsData.user ? detailsData.user.phone : ''}}</span>
        <span>服务地址：{{detailsData.addr_info ? detailsData.addr_info.addr : ''}}</span>
      </div>
    </div>
    <div class="div_aunt">
      <div class="employer" >阿姨信息</div>
      <div class="employer_info">
        <span>姓名：{{detailsData.service ? detailsData.service.name : ''}}</span>
        <span>手机号码：{{detailsData.service ? detailsData.service.phone : ''}}</span>
        <span>服务类型：{{detailsData.service ? detailsData.service.service_type.title : ''}}</span>
        <span>服务薪资：{{detailsData.old_user_agency ? detailsData.old_user_agency : ''}}</span>
      </div>
    </div>
    <div class="div_service_order">
      <div class="employer" >服务订单信息</div>
       <!-- 表格 -->
        <div id="table" >
      <el-table :data="tableData" border>
        <el-table-column prop="child_order" label="子订单号" align="center"></el-table-column>
         <el-table-column prop="mother_order" label="签约人" align="center"></el-table-column>
        <el-table-column prop="phone" label="手机号码" align="center"></el-table-column>
        <el-table-column prop="service_people" label="服务人员" align="center"></el-table-column>
        <el-table-column prop="service_addr" label="服务地址" align="center"></el-table-column>
        <el-table-column prop="service_type" label="服务类型" align="center"></el-table-column>
        <el-table-column prop="aunt_salary" label="阿姨工资" align="center"></el-table-column>
        <el-table-column prop="service_money" label="服务费" align="center" width="120"></el-table-column>
         <el-table-column prop="distribution1" label="分销1" align="center"></el-table-column>
          <el-table-column prop="distribution2" label="分销2" align="center"></el-table-column>
      <el-table-column prop="salary_pay" label="薪资支付" align="center"></el-table-column>
      <el-table-column prop="order_status" label="订单状态" align="center"></el-table-column>
      </el-table>
    </div>
    </div>
    <!-- <div class="div_chat">
      <div class="employer" >聊天记录</div>
      <div class="">
       <el-button style="margin-top:10px" @click="clickDispose">介入处理</el-button>
       <div v-for="(val,index) in chatList" v-bind:key="index" style="margin-top:10px">
          <span >{{val.date}}</span>
          <span @click="showChat">聊天记录</span>
          <span @click="showChat">点击查看</span>
       </div>
      </div>
    </div> -->
    <div class="div_result">
      <div class="employer" >申诉结果</div>
      <div class="employer_info">
        <span>2019-05-20  15：00</span>
        <span>得贤管家介入</span>
      </div>
    </div>
  </div>
</template>
<script>
import {getComplainDetails} from '../../../service/complainService'
export default {
  data () {
    return {
      tableData: [],
      detailsData: '',
      chatList: [
        {
          date: 1
        }
      ]
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['投诉管理', '订单申诉', '申诉详情'])
  },
  methods: {
    // 订单详情
    getComplainDetails () {
      this.order_code = 20190603165454508409
      getComplainDetails(this.order_code)
        .then(res => {
          this.detailsData = res.data.data ? res.data.data : ''
        })
    },
    // 点击查看聊天记录
    showChat () {
    },
    // 点击介入处理
    clickDispose () {
      this.$router.push('/interveneDispose')
    }
  },
  mounted () {
    this.getComplainDetails()
  }
}
</script>
<style scoped>
.div_emploter,.div_aunt,.div_service_order{
  padding-top: 20px;
  margin-left: 20px;
  box-sizing: border-box;
  background: inherit;
  width: 90%;
  text-align: left;
  padding-bottom: 20px;
  border-bottom: 2px  dotted #999;
}
.div_result,.div_chat {
  padding-top: 20px;
  margin-left: 20px;
  box-sizing: border-box;
  background: inherit;
  width: 90%;
  text-align: left;
}
.div_chat span{
  padding-right: 15px;
  color: rgb(79, 79, 221);
  font-weight: 700;
}
.employer_info {
  display: inline-flex;
  margin-top: 10px;
}
.employer_info span {
  padding-right: 60px;
}
.employer {
  font-weight: 600;
  font-size: 18px;
}
#table {
  margin-top: 18px;
}
.el-table >>> th {
    background-color: #e3eefa;
    font-family: "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53";
    color: #000000;
    font-size: 16px;
    font-weight: bold;
    border:none;
}
.el-table >>> th .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-table >>> th:first-child .cell {
  border-left: transparent solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
</style>



