import instance from './http.config'
// 申诉列表
export const getComplainList = (param) => instance.get(`/admin/orders/complaint_orders?${param}`)
// 申诉详情
export const getComplainDetails = (param) => instance.get(`/admin/orders/complaint_order_info?order_code=${param}`)
// 服务类型列表
export const getServiceTypeList = () => instance.get(`/admin/service_type/index`)



